.forecast {
    flex-direction: row;
    gap: 10px;
}

.forecast li {
    list-style: none;
    flex-direction: column;
    padding: 0 2px 5px;
    text-align: center;
}

.forecast li img {
    transform: scale(0.8);
    padding-top: 5px;
}

.forecast li span,
.forecast li p {
    text-align: center;
    padding-top: 5px;
    font-size: 18px;
}
